html,
body,
#root {
  height: 100%;
}

@font-face {
  font-family: 'JetBrainsMono';
  src:
    local('JetBrainsMono'),
    url(./fonts/ttf/JetBrainsMono-Regular.ttf) format('truetype'),
    url(./fonts/webfonts/JetBrainsMono-Regular.woff2) format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'JetBrainsMono';
  src:
    local('JetBrainsMono'),
    url(./fonts/ttf/JetBrainsMono-Italic.ttf) format('truetype'),
    url(./fonts/webfonts/JetBrainsMono-Italic.woff2) format('woff2');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'JetBrainsMono';
  src:
    local('JetBrainsMono'),
    url(./fonts/ttf/JetBrainsMono-Bold.ttf) format('truetype'),
    url(./fonts/webfonts/JetBrainsMono-Bold.woff2) format('woff2');
  font-weight: 700;
  font-style: normal;
}